import { CommonClassItem, ItemData } from '../CommonClassItem';
import { CachedFilmData, StringNormalizer } from '../CacheTypes';
import { CacheManager } from '../CacheManager';

export interface FilmData extends ItemData {
  titre_affiche: string;
  titre_original?: string;
  titre_francais?: string;
  annee?: number;
  auteurs?: string;
}

export class Film extends CommonClassItem {
  static readonly minName = 'film';
  
  // Cache manager spécifique aux films
  private static _cacheManagerInstance: CacheManager<FilmData, CachedFilmData> = new CacheManager();
  
  protected static get cacheManager(): CacheManager<FilmData, CachedFilmData> {
    return this._cacheManagerInstance;
  }
  
  static readonly ERRORS = {
    'no-items': 'Aucun film dans la base, bizarrement…',
  };

  static formateProp(prop: string, value: any): string {
    switch(prop) {
      case 'annee':
        return value ? value.toString() : '';
      default: 
        return value || '';
    }
  }

  /**
   * Prépare un film pour le cache de recherche
   * SEULE méthode spécifique - le reste hérite de CommonClassItem !
   */
  static prepareItemForCache(film: FilmData): CachedFilmData {
    // Créer un array avec tous les titres disponibles
    const titres: string[] = [];
    if (film.titre_affiche) {
      titres.push(film.titre_affiche);
    }
    if (film.titre_original) {
      titres.push(film.titre_original);
    }
    if (film.titre_francais) {
      titres.push(film.titre_francais);
    }
    
    // Versions minuscules pour recherche
    const titres_min = titres.map(titre => StringNormalizer.toLower(titre));
    
    return {
      id: film.id,
      titre_affiche: film.titre_affiche,
      titre_original: film.titre_original,
      titre_francais: film.titre_francais,
      titres: titres,
      titres_min: titres_min,
      annee: film.annee,
      auteurs: film.auteurs,
      auteurs_min: film.auteurs ? StringNormalizer.toLower(film.auteurs) : undefined
    };
  }

  /**
   * Recherche de films par titre (optimisée)
   * Méthode spécifique Film
   */
  static searchByTitle(searchTerm: string): CachedFilmData[] {
    const searchLower = StringNormalizer.toLower(searchTerm);
    
    return this.filter((film: any) => {
      return film.titres_min.some((titre: string) => titre.includes(searchLower));
    }) as CachedFilmData[];
  }

  /**
   * Recherche de films par auteur
   * Méthode spécifique Film
   */
  static searchByAuthor(author: string): CachedFilmData[] {
    const authorLower = StringNormalizer.toLower(author);
    
    return this.filter((film: any) => {
      return film.auteurs_min ? film.auteurs_min.includes(authorLower) : false;
    }) as CachedFilmData[];
  }
  
  // Méthodes typées pour plus de confort (optionnel)
  static get(id: string): CachedFilmData | null {
    return super.get(id) as CachedFilmData | null;
  }
  
  static getAll(): CachedFilmData[] {
    return super.getAll() as CachedFilmData[];
  }
}
